import { Modal, Tabs, Tab } from "react-bootstrap";
import React, { useState } from "react";
import { Form, Input, Radio, DatePicker } from "antd";
import "./LoginModal.scss";
import { CloseOutlined } from "@ant-design/icons";
import { useDispatch } from "react-redux";
import {
  loginCandidateProAction,
  registerCandidateAction
} from "state/actions/authenticationActions";

const config = {
  rules: [
    { type: "object", required: true, message: "Vui lng chn ngy sinh!" }
  ]
};

const validateMessages = {
  required: "Vui lng nhp ${label}!",
  types: {
    email: "Email khng hp l",
    password: "Mt khu"
  }
};

function LoginModal({ show, toggleModal }) {
  const [loading, setLoading] = useState(false);

  const dispatch = useDispatch();

  //Handle submit Login
  const onFinish = (values) => {
    setLoading(true);

    dispatch(loginCandidateProAction({ user: values.user }))
      .then(() => {
        toggleModal();
      })
      .catch(() => {
        setLoading(false);
      });
  };

  const onSignupFinish = (fieldsValue) => {
    const values = {
      ...fieldsValue,
      dateOfBirth: fieldsValue["dateOfBirth"].format("YYYY-MM-DD")
    };

    delete values.confirm;

    setLoading(true);

    dispatch(registerCandidateAction(values)).catch(() => {
      setLoading(false);
    });
  };

  return (
    <Modal show={show} onHide={toggleModal} dialogClassName="ir-modal">
      <Modal.Body>
        <button className="btn-close text-gray" onClick={toggleModal}>
          <CloseOutlined />
        </button>
        <div>
          <Tabs defaultActiveKey="login">
            <Tab eventKey="login" title="ng nhp" disable={loading}>
              <div className="text-note text-center text-dark-gray">
                ng nhp hoc ng k  ng tuyn cng vic ny!
              </div>
              <Login onFinish={onFinish} loading={loading} />
            </Tab>
            <Tab eventKey="signup" title="ng k" disable={loading}>
              <div className="text-note text-center text-dark-gray">
                ng nhp hoc ng k  ng tuyn cng vic ny!
              </div>
              <Signup onFinish={onSignupFinish} loading={loading} />
            </Tab>
          </Tabs>
        </div>
      </Modal.Body>
    </Modal>
  );
}

export default LoginModal;

const Login = ({ onFinish, loading }) => (
  <Form
    layout="vertical"
    name="nest-messages"
    validateMessages={validateMessages}
    onFinish={onFinish}
    className="hr-login__container__left__form"
  >
    {/* Email */}
    <Form.Item
      label="Email"
      name={["user", "email"]}
      rules={[{ type: "email", required: true }]}
    >
      <Input
        className="hr-login__container__left__form__input"
        placeholder="Nhp email"
      />
    </Form.Item>

    {/* Password  */}
    <Form.Item
      label="Mt khu"
      name={["user", "password"]}
      rules={[{ required: true }]}
    >
      <Input.Password
        className="hr-login__container__left__form__input"
        placeholder="Nhp mt khu"
      />
    </Form.Item>

    {/* <Link to="/sign-up/hr">ng k ti khon</Link> */}

    {/* Button Login  */}
    <button htmlType="submit" className="hr-login__container__left__form__btn">
      ng nhp
      {loading && <div className="dashed-loading"></div>}
    </button>
  </Form>
);

const Signup = ({ onFinish, loading }) => (
  <Form
    layout="vertical"
    name="nest-messages"
    validateMessages={validateMessages}
    onFinish={onFinish}
    className="candidate-register__container__left__form"
  >
    {/* Fullname */}
    <Form.Item label="H v tn" name="fullName" rules={[{ required: true }]}>
      <Input
        className="candidate-register__container__left__form__input"
        placeholder="H v tn"
      />
    </Form.Item>

    {/* Email */}
    <Form.Item
      label="Email"
      name="email"
      rules={[{ type: "email", required: true }]}
    >
      <Input
        className="candidate-register__container__left__form__input"
        placeholder="Email"
      />
    </Form.Item>

    {/* Password  */}
    <Form.Item
      name="password"
      rules={[
        {
          required: true,
          message: "Vui lng nhp mt khu!"
        }
      ]}
      hasFeedback
    >
      <Input.Password
        className="candidate-register__container__left__form__input"
        placeholder="Mt khu"
      />
    </Form.Item>

    {/* Confirm password  */}
    <Form.Item
      name="confirm"
      dependencies={["password"]}
      hasFeedback
      rules={[
        {
          required: true,
          message: "Vui lng xc nhn mt khu!"
        },
        ({ getFieldValue }) => ({
          validator(rule, value) {
            if (!value || getFieldValue("password") === value) {
              return Promise.resolve();
            }
            return Promise.reject(
              "Password khng trng khp, vui lng kim tra li!"
            );
          }
        })
      ]}
    >
      <Input.Password
        placeholder="Xc nhn mt khu"
        className="candidate-register__container__left__form__input"
      />
    </Form.Item>

    <div className="candidate-register__container__left__form__group">
      {/* Date of birth */}
      <Form.Item name="dateOfBirth" label="Ngy sinh" {...config}>
        <DatePicker
          placeholder="Ngy sinh"
          className="candidate-register__container__left__form__input"
        />
      </Form.Item>

      {/* Gender  */}
      <Form.Item
        rules={[{ required: true, message: "Vui lng chn gii tnh!" }]}
        name="gender"
      >
        <Radio.Group>
          <Radio value={true}>Nam</Radio>
          <Radio value={false}>N</Radio>
        </Radio.Group>
      </Form.Item>
    </div>
    {/* Phonenumber */}
    <Form.Item
      label="S in thoi"
      name="phone"
      rules={[
        { required: true },
        {
          pattern: /^[\d]{0,11}$/,
          message: "S in thoi ti a 11 s"
        },
        { min: 10, message: "S in thoi phi t 10-11 s" }
      ]}
    >
      <Input
        className="candidate-register__container__left__form__input"
        placeholder="S in thoi"
        type="number"
      />
    </Form.Item>

    {/* Button Login  */}
    <button
      htmlType="submit"
      className="candidate-register__container__left__form__btn"
    >
      ng k
      {loading && <div className="dashed-loading"></div>}
    </button>
  </Form>
);
