import React, { useEffect, useState } from "react";
import {
  FundViewOutlined,
  FileDoneOutlined,
  HeartOutlined,
  DeleteFilled,
  CloseOutlined
} from "@ant-design/icons";
import Widget from "components/Widget/Widget";
import {
  closeJob,
  deleteJobPost,
  hrGetJobDetail
} from "services/hrJobServices";
import { useSelector } from "react-redux";
import ContentLoader from "react-content-loader";
import { formatDateTime, toast, toastErr } from "utils/index";
import { Button } from "antd";
import { EditOutlined } from "@ant-design/icons";
import history from "state/history";
import swal from "sweetalert";

function HRJobDetail({ id }) {
  const [post, setPost] = useState({});
  const [loading, setLoading] = useState(false);

  const { token } = useSelector((state) => state.auth.recruiter);

  useEffect(() => {
    setLoading(true);
    const fetchData = async () => {
      await hrGetJobDetail(id, token)
        .then((res) => {
          const { data } = res.data;

          setPost(data);
        })
        .catch((err) => {
          toastErr(err);
        })
        .finally(() => {
          setLoading(false);
        });
    };

    fetchData();
  }, []);

  const handleCloseJp = () => {
    swal({
      title: "Bn c chc khng?",
      text: "Mt khi ng, bn khng th khi phc thng tin  chn!",
      icon: "warning",
      buttons: ["Hu", "Tip tc"],
      dangerMode: true
    })
      .then(async (willDelete) => {
        if (willDelete) {
          setLoading(true);

          await closeJob(id, token)
            .then(() => {
              toast({ message: "ng tin tuyn dng thnh cng" });
              history.push("/recruiter/jobs");
            })
            .catch((err) => toastErr(err))
            .finally(() => setLoading(false));
        } else {
          swal("Chc mng d liu ca bn vn an ton!");
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const handleDelete = () => {
    swal({
      title: "Bn c chc khng?",
      text: "Mt khi xo, bn khng th khi phc nhng tin  chn!",
      icon: "warning",
      buttons: ["Hu", "Xo"],
      dangerMode: true
    })
      .then(async (willDelete) => {
        if (willDelete) {
          const ids = [id];

          setLoading(true);
          await deleteJobPost(ids, token)
            .then((res) => {
              const { message } = res.data;
              toast({ message });

              history.push("/recruiter/jobs");
            })
            .catch((err) => {
              console.log(err);
            })
            .finally(() => {
              setLoading(false);
            });
        } else {
          swal("Chc mng d liu ca bn vn an ton!");
        }
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const { total_view, total_save, total_apply } = post;

  return (
    <div className="container">
      <div className="row">
        <div className="col-md-3">
          <div className="jp-summary">Thng k ng vin</div>
          <Widget
            title="Lt truy cp"
            value={Intl.NumberFormat().format(total_view)}
            icon={<FundViewOutlined style={{ fontSize: 48 }} />}
          />
          <Widget
            title="Lt ng tuyn"
            value={Intl.NumberFormat().format(total_apply)}
            icon={<FileDoneOutlined style={{ fontSize: 48 }} />}
          />
          <Widget
            title="Lt lu tin tuyn dng"
            value={Intl.NumberFormat().format(total_save)}
            icon={<HeartOutlined style={{ fontSize: 48 }} />}
          />
        </div>
        <div className="col-md-9" style={{ paddingRight: 0 }}>
          <div className="panel panel--light">
            <div className="panel-body">
              <div className="jp-header">
                <h5>Thng tin chi tit</h5>
                <div>
                  <Button
                    icon={<EditOutlined />}
                    onClick={() => history.push(`/recruiter/jobs/${id}/edit`)}
                    className="mr-5"
                  >
                    Chnh sa tin
                  </Button>
                  <Button
                    icon={<CloseOutlined />}
                    onClick={handleCloseJp}
                    className="mr-5"
                  >
                    ng tin
                  </Button>
                  <Button icon={<DeleteFilled />} onClick={handleDelete}>
                    Xo tin
                  </Button>
                </div>
              </div>
              <div>
                <Detail {...post} loading={loading} />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default HRJobDetail;

const Detail = ({
  job_title,
  job_domain,
  salary,
  posted_in,
  deadline,
  contract_type,
  amount,
  description,
  requirement,
  benefit,
  loading
}) => (
  <table className="jb-detail">
    <tbody>
      <tr>
        <td className="jp-label">Tiu </td>
        <td className="jp-value">{!loading ? job_title : <MyLoader />}</td>
      </tr>
      <tr>
        <td className="jp-label">Ngnh</td>
        <td className="jp-value">{!loading ? job_domain : <MyLoader />}</td>
      </tr>
      <tr>
        <td className="jp-label">Ngy ng tin</td>
        <td className="jp-value">
          {!loading ? posted_in && formatDateTime(posted_in) : <MyLoader />}
        </td>
      </tr>
      <tr>
        <td className="jp-label">Hn cht np h s</td>
        <td className="jp-value">
          {!loading ? deadline && formatDateTime(deadline) : <MyLoader />}
        </td>
      </tr>
      <tr>
        <td className="jp-label">Hnh thc lm vic</td>
        <td className="jp-value">{!loading ? contract_type : <MyLoader />}</td>
      </tr>
      <tr>
        <td className="jp-label">Lng</td>
        <td className="jp-value">{!loading ? salary : <MyLoader />}</td>
      </tr>
      <tr>
        <td className="jp-label">a im lm vic</td>
        <td className="jp-value">Thnh ph H Ch Minh</td>
      </tr>
      <tr>
        <td className="jp-label">S lng cn tuyn</td>
        <td className="jp-value">{!loading ? amount : <MyLoader />}</td>
      </tr>
      <tr>
        <td className="jp-label">M t cng vic</td>
        <td className="jp-value">
          {!loading ? (
            <div dangerouslySetInnerHTML={{ __html: description }}></div>
          ) : (
            <MyLoader />
          )}
        </td>
      </tr>
      <tr>
        <td className="jp-label">Yu cu ng vin</td>
        <td className="jp-value">
          {!loading ? (
            <div dangerouslySetInnerHTML={{ __html: requirement }}></div>
          ) : (
            <MyLoader />
          )}
        </td>
      </tr>
      <tr>
        <td className="jp-label">Quyn li ng vin</td>
        <td className="jp-value">
          {!loading ? (
            <div dangerouslySetInnerHTML={{ __html: benefit }}></div>
          ) : (
            <MyLoader />
          )}
        </td>
      </tr>
    </tbody>
  </table>
);

const MyLoader = (props) => (
  <ContentLoader
    speed={2}
    width={400}
    height={18}
    viewBox="0 0 400 18"
    backgroundColor="#f3f3f3"
    foregroundColor="#ecebeb"
    {...props}
  >
    <rect x="0" y="0" rx="0" ry="0" width="400" height="18" />
  </ContentLoader>
);
