import { Form, Input, Radio } from "antd";
import React from "react";
import { Link, Redirect } from "react-router-dom";
import { useDispatch } from "react-redux";

import { registerHrAction } from "state/actions/authenticationActions";
import { checkCookie } from "utils/cookies";
import "./HRSignUp.scss";

const validateMessages = {
  required: "Vui lng nhp ${label}!",
  types: {
    email: "Email khng hp l",
    password: "Mt khu"
  }
};

function HRSignUp() {
  const dispatch = useDispatch();

  //Handle submit Login
  const onFinish = (value) => {
    dispatch(registerHrAction(value));
  };

  return checkCookie("recruiter_token") ? (
    <Redirect to="/" />
  ) : (
    <div className="hr-register">
      <div className="hr-register__container">
        <div className="hr-register__container__logo">
          <Link
            to="/recruiter"
            className="hr-register__container__logo__wrapper"
          >
            <h3>
              Automated&nbsp;<span>Screening</span>
            </h3>
          </Link>
        </div>

        {/* Content  */}
        <div className="hr-register__container__form">
          <span className="hr-register__container__form__title">
            Nh tuyn dng ng&nbsp;k
          </span>
          {/* 
          <p className="hr-register__container__form__bigLabel">
            Thng tin ng nhp
          </p> */}

          <Form
            layout="vertical"
            name="nest-messages"
            validateMessages={validateMessages}
            onFinish={onFinish}
            className="hr-register__container__form__confirm"
          >
            {/* Fullname */}
            <Form.Item
              label="H v tn"
              name="fullName"
              rules={[{ required: true }]}
            >
              <Input
                className="hr-register__container__form__confirm__input"
                placeholder="H v tn"
              />
            </Form.Item>
            {/* Email */}
            <Form.Item
              label="Email"
              name="email"
              rules={[{ type: "email", required: true }]}
            >
              <Input
                className="hr-register__container__form__confirm__input"
                placeholder="Email"
              />
            </Form.Item>
            {/* Password  */}
            <Form.Item
              name="password"
              rules={[
                {
                  required: true,
                  message: "Vui lng nhp mt khu!"
                }
              ]}
              hasFeedback
            >
              <Input.Password
                className="hr-register__container__form__confirm__input"
                placeholder="Mt khu"
              />
            </Form.Item>
            <Form.Item
              name="confirm"
              dependencies={["password"]}
              hasFeedback
              rules={[
                {
                  required: true,
                  message: "Vui lng xc nhn mt khu!"
                },
                ({ getFieldValue }) => ({
                  validator(rule, value) {
                    if (!value || getFieldValue("password") === value) {
                      return Promise.resolve();
                    }
                    return Promise.reject(
                      "Password khng trng khp, vui lng kim tra li!"
                    );
                  }
                })
              ]}
            >
              <Input.Password
                placeholder="Xc nhn mt khu"
                className="hr-register__container__form__confirm__input"
              />
            </Form.Item>
            {/* Phonenumber */}
            <Form.Item
              label="S in thoi"
              name="phone"
              rules={[
                { required: true },
                {
                  pattern: /^[\d]{0,11}$/,
                  message: "S in thoi ti a 11 s"
                },
                { min: 10, message: "S in thoi phi t 10-11 s" }
              ]}
            >
              <Input
                className="hr-register__container__form__confirm__input"
                placeholder="S in thoi"
                type="number"
              />
            </Form.Item>
            <Form.Item
              rules={[{ required: true, message: "Vui lng chn gii tnh!" }]}
              name="gender"
            >
              <Radio.Group>
                <Radio value={true}>Nam</Radio>
                <Radio value={false}>N</Radio>
              </Radio.Group>
            </Form.Item>

            <p className="hr-register__container__form__confirm__text">
               c ti khon?{" "}
              <Link to="/recruiter/sign-in">
                <strong>ng nhp ngay</strong>
              </Link>
            </p>
            {/* Button Login  */}
            <button
              htmlType="submit"
              className="hr-register__container__form__confirm__btn"
            >
              ng k
              {/* {isLoading && <div className="dashed-loading"></div>} */}
            </button>
          </Form>
        </div>
      </div>
    </div>
  );
}

export default HRSignUp;
